<template>
  <div class="college-full-screen college">
    <div
      style="width:120px;height:28px;border:1px solid #03b187;border-radius:14px;cursor: pointer;text-align: center;line-height: 28px; margin: 20px 0 0 30px"
      @click="goback"
      v-if="this.$route.query.id"
    >
      <i style="color:#03b187" class="el-icon-back"></i>
      <span style="color: #03b187">返回列表</span>
    </div>
    <div class="college_content">
      <div class="college_head">
        <div class="college_head_lf">
          <div class="circle circle_bg">
            <!-- <img src="@/assets/img/xdlogo.jpg" /> -->
            <svgIcon icon-class="iconzhuanye1"></svgIcon>
          </div>
          <label>{{academyobj.name}}</label>
          <span>（ {{academyobj.code}} ）</span>
        </div>
        <div class="college_head_rt cl-box">
          <h4 class="cl-text">学院简介</h4>
          <p class="cl-text">{{academyobj.intro}}</p>
        </div>
      </div>
      <div class="college_card">
        <ul>
          <li v-for="(vo, index) in card" :key="index">
            <div class="cart_lf">
              <img :src="vo.imgUrl" />
            </div>
            <div class="cart_rt">
              <span>{{vo.num}}</span>
              <label>{{vo.name}}</label>
            </div>
          </li>
        </ul>
      </div>
      <div class="college_search">
        <div class="college_search_lf">
          <span class="cl-btn">专业概况</span>
        </div>
        <div class="college_search_rt">
          <el-select v-model="page.certification" placeholder="请选择认证状态" @change="changeauth">
            <el-option v-for="item in options" :key="item.id" :label="item.name" :value="item.id"></el-option>
          </el-select>
        </div>
      </div>
      <div class="college_tabel cl-tabel">
        <el-table :data="tableData" stripe style="width: 100%">
          <el-table-column prop="code" label="专业代码"></el-table-column>
          <el-table-column prop="specialtyName" label="专业名称"></el-table-column>
          <el-table-column prop="academyName" label="所属院校"></el-table-column>
          <el-table-column prop="intro" label="专业介绍" width="600"></el-table-column>
          <el-table-column prop="realName" label="专业负责人"></el-table-column>
          <el-table-column label="认证状态">
            <template slot-scope="scope">
              <!-- <div>{{scope.row.authenticationStatus}}</div> -->
              <div v-if="scope.row.authenticationStatus === 1">未认证</div>
              <div v-if="scope.row.authenticationStatus === 0">已认证</div>
              <div v-if="scope.row.authenticationStatus === 2">已申请</div>
            </template>
          </el-table-column>
          <el-table-column label="操作" width="150">
            <template slot-scope="scope">
              <div class="cl-operate-button distance">
                <SvgIcon icon-class="iconchakanwenjuan"></SvgIcon>
                <el-button
                  @click.native.prevent="operateRow(scope.row.id)"
                  type="text"
                  size="small"
                >专业画像</el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <div class="college_pagination">
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            background
            :current-page="page.page"
            :page-sizes="[15,30,40,50]"
            :page-size="page.rows"
            layout="prev, pager, next, jumper, sizes, total"
            :total="total"
          ></el-pagination>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Self from "@/api/self";
export default {
  name: "College",
  components: {},
  data() {
    return {
      academyobj: {},
      card: [
        {
          imgUrl: require("@/assets/img/img-yiliuzhuanye.png"),
          num: "",
          name: "省级一流专业"
        },
        {
          imgUrl: require("@/assets/img/img-yitongguo.png"),
          num: "",
          name: "已通过认证专业"
        },
        {
          imgUrl: require("@/assets/img/img-yishenqing.png"),
          num: "",
          name: "已申请认证专业"
        },
        {
          imgUrl: require("@/assets/img/img-weirenzheng.png"),
          num: "",
          name: "未认证专业"
        }
      ],
      options: [
        {
          name: "未认证",
          id: 1
        },
        {
          name: "已认证",
          id: 0
        },
        {
          name: "已申请",
          id: 2
        }
      ],
      intro: "",
      tableData: [],
      total: 0,
      pageSize: 15,
      pageNum: 1,
      page: {
        certification: "",
        page: 1,
        rows: 15
      },
      name: "",
      academy: ""
    };
  },
  created() {
    // this.initData()
    this.getacademy();
  },
  mounted() {},
  methods: {
    goback() {
      this.$router.push({
        path: "/professional/collegePortrait"
      });
    },
    //  学院/basic/specialty/portrayal/academy
    getacademy() {
      let obj = {}
      if(this.$route.query.id) {
        obj = {
          academyId: this.$route.query.id
        }
      } else {
        obj = {
          academyId: 0
        }
      }
      this.$get("/basic/specialty/portrayal/academy", obj).then(res => {
        this.academyobj = res.data.academy;
        this.card[0].num = res.data.pass;
        this.card[1].num = res.data.isfirst;
        this.card[2].num = res.data.apply;
        this.card[3].num = res.data.nopass;
        this.academy = res.data.academy.id;
        this.initData(res.data.academy.id);
      });
    },
    changeauth() {
      this.initData(this.academy);
    },
    initData(a) {
      let key = {
        page: this.page.page,
        rows: this.page.rows,
        academyId: a,
        authenticationStatus: this.page.certification
      };
      this.$post("/basic/specialty/portrayal/specialtyList", key).then(res => {
        if (res.status == 1) {
          this.tableData = res.data.list;
          this.total = Number(res.data.total);
          // this.loading = false;
        }
      });
    },
    operateRow(id) {
      this.$router.push({
        path: "/professionalSystem/collegeSpecialty",
        query: {
          id: id
        }
      });
    },
    handleSizeChange(val) {
      this.page.rows = val;
      this.initData();
    },
    handleCurrentChange(obj) {
      this.page.page = obj;
      this.initData();
    }
  }
};
</script>

<style lang="scss">
@import "../../styles/college.scss";
.circle_bg{
  position:relative;
  .svg-icon{
     font-size:80px;
     position:absolute;
     left:0;
     right:0;
     bottom:0;
     top:0;
     z-index:10;
     margin:auto;
  }
  &:after{
    content:'';
    display:block;
    width:80%;
    height:80%;
    background:#fff;
    border-radius:50%;
    position:absolute;
    left:0;
    right:0;
    top:0;
    bottom:0;
    margin:auto;
    z-index:2;
  }
}
</style>



